.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 24px 0;
  background-color: $color-transparent;

  @include viewport--sm {
    padding: 16px 0;
  }
}

.header__logo {
  display: block;

  svg {
    display: block;

    @include viewport--sm {
      width: 110px;
      height: 24px;
    }
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
